
.cat-radio-wrapper, .cat-radio {
  display: inline-block;
  position: relative;
  white-space: nowrap;
  cursor: pointer;
}
.cat-radio-wrapper {
  font-size: 14px;
  margin-right: 8px;
}
.cat-radio-wrapper:hover .cat-radio-inner {
    border-color: #3da8f5;
}
.cat-radio-wrapper-disabled {
    cursor: not-allowed;
    color: #cccccc;
}
.cat-radio-wrapper-disabled:hover .cat-radio-inner {
      border-color: #cccccc;
}
.cat-radio {
  outline: none;
  line-height: 1;
  vertical-align: text-bottom;
}
.cat-radio .cat-radio-input {
    position: absolute;
    left: 0;
    z-index: 1;
    cursor: pointer;
    opacity: 0;
    top: 0;
    bottom: 0;
    right: 0;
    margin: 0;
}
.cat-radio .cat-radio-inner {
    position: relative;
    top: 0;
    left: 0;
    display: block;
    width: 16px;
    height: 16px;
    border-radius: 14px;
    border: 1px solid #cccccc;
    background-color: #ffffff;
    -webkit-transition: all .3s;
    transition: all .3s;
}
.cat-radio .cat-radio-inner:after {
      position: absolute;
      width: 8px;
      height: 8px;
      left: 3px;
      top: 3px;
      border-radius: 4px;
      display: table;
      border-top: 0;
      border-left: 0;
      content: " ";
      background-color: #3da8f5;
      opacity: 0;
      -webkit-transform: scale(0);
              transform: scale(0);
      -webkit-transition: all 0.3s cubic-bezier(0.78, 0.14, 0.15, 0.86);
      transition: all 0.3s cubic-bezier(0.78, 0.14, 0.15, 0.86);
}
.cat-radio-checked .cat-radio-inner {
    border-color: #3da8f5;
}
.cat-radio-checked .cat-radio-inner:after {
      -webkit-transform: scale(1);
              transform: scale(1);
      opacity: 1;
}
.cat-radio-disabled {
    cursor: not-allowed;
}
.cat-radio-disabled .cat-radio-input {
      cursor: not-allowed;
}
.cat-radio-disabled .cat-radio-inner {
      border-color: #cccccc;
}
.cat-radio-disabled .cat-radio-inner:after {
        background-color: #cccccc;
}
.cat-radio-label {
  padding: 0 8px;
}



